* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

.header {
  text-align: center;
  background-color: #000000;
  min-height: 20%;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: sticky;
  top: 0;
  margin-bottom: 0;
  margin: 0;
  padding: 0;
  z-index: 1;
}

/* .App-link {
  color: #61dafb;
} */
.footer {
  background-color: #000000;
  min-height: 5%;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  /* align-items: center;
  justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
  /* position: sticky; */
  bottom: 0;
  margin: 0;
  position: relative;
  z-index: 1;

}

.footer-text {
  text-align: left;
}

.footer-icon {
    align-content: right;
    text-align: right;
    height: 45px;
    width: 45px;
    right: 20px;
    
}



.header-buttons {
  margin: 5px;
  background-color: #209bd7;
  border: 2px solid gray;
  margin-bottom: 16px;
  border-radius: 10px;
  font-size: 25px;
  width: 100px;
  text-align: center;
  transition: .2s ease;
}

.header-buttons:hover{
  transform: rotate(15deg);
  
  cursor: pointer;
}

.static-background {
  position:fixed;
  top: 0;
  left:0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  z-index: -2;
}



.page-background {
  background-color: #209ad7f1;
  min-height: 100vh;
  background-size: cover;
  
  margin: 0;
  padding: 0;
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 0%;
  position: relative;
  z-index: 0;
}


.content-container {
  margin-left: 1%;
  margin-right: 1%;
  padding-top: 2%;
  
  text-align: center;
  align-content: center;
  display: flex;
  position: relative;
  float: none;
  flex-wrap: wrap;
  justify-content: space-between;

}

.container-row {
  display:table;
        clear:both;
        width:100%;
        content:"";
}


.container-col-left{
  width: 30%;
float:left;
}

.container-col-right{
  width: 70%;
  float:left;

}

.component-container{
  margin: 2%;
  padding: 2%;
  background-color: #ffffff;
  width: 95%;
  height: min-content;
  position: relative;
  overflow: hidden;
  text-align: center;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  
}


@media screen and (max-width: 1000px) {
  .container-col-left {
    width: 100%;
  }
  .container-col-right {
    width: 100%;
  }
  .component-container {
    width: 100%; /* Stack containers on smaller screens */
    margin: 2% 0; /* Adjust margin for stacked layout */
  }
  .page-background{
    background-color: #00000000 !important;
  }
}
.bio-icon{
  width: 55%;
  height: auto;
  position: relative;


}

.video-container {
  
  position: relative;
  width: 100%;
  max-width: 1280px;
  padding-top: 56.25%;
  overflow: hidden; /* Ensure no overflow */
}

.video-element {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: transform .5s ease-in-out;
  object-fit: cover;
  transform: translateX(100%); /* Start offscreen to the right */

}

.video-element.active {
  transform: translateX(0); /* Slide into view */
}

.video-element.exit {

  transform: translateX(-100%); /* Slide out to the left */
}

